/** 
 * 
 * https://cn.vuejs.org/v2/guide/components-registration.html
*/
import Vue from 'vue'
import Test from './test/';

export default (() => {
  const list = [
    Test
  ]
  let result = {}
  //
  list.forEach(ele => {
    ele.mixins = [window.AVUE.$Echart]
    result[`avue-echart-${ele.name}`] = ele
  })
  return result
})()
